input[type="checkbox"] {
  position: relative;
  width: 100px;
  height: 30px;
  margin: 0;
  -webkit-appearance: none;
  outline: none;
  transition: 0.5s;
  cursor: pointer;
}
.span {
  position: absolute;
  top: 0;
  width: 50%;
  height: 100%;
  line-height: 30px;
  height: 30px;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  pointer-events: none;
  color: #fff;
  transform-style: preserve-3d;
  text-shadow: 2px 2px 4px #000000;
}
.span:nth-child(2) {
  left: 0;
  border-top-left-radius: 40px;
  border-bottom-left-radius: 40px;
  background: #07943b;
}
.span:nth-child(2)::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, #545454, #707070);
  border-top-left-radius: 40px;
  border-bottom-left-radius: 40px;
  transform-origin: right;
  transition: 1s;
  backface-visibility: hidden;
}
input[type="checkbox"]:checked ~ .span:nth-child(2)::before {
  transform: rotateY(180deg);
}
.span:nth-child(3) {
  right: 0;
  border-top-right-radius: 40px;
  border-bottom-right-radius: 40px;
  background: #545454;
}
.span:nth-child(3)::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, #08bf4e, #07943b);
  border-top-right-radius: 40px;
  border-bottom-right-radius: 40px;
  transform-origin: left;
  transition: 1s;
  backface-visibility: hidden;
  transform: rotateY(180deg);
}
input[type="checkbox"]:checked ~ .span:nth-child(3)::before {
  transform: rotateY(360deg);
}

