.editor {
  display: flex;
  flex: 1;
  flex-direction: column;
  width: 100%;
  
  background: #fff;
}

.editorHd {
  padding: 8px;
  border: 1px solid #e6e9ed;
}

.editorBd {
  flex: 1;
}

.editorSidebar,
.editorContent {
  display: flex;
  flex-direction: column;
}

.editorSidebar {
  background: #fafafa;
}

.editorSidebar:first-child {
  border-right: 1px solid #e6e9ed;
}

.editorSidebar:last-child {
  border-left: 1px solid #e6e9ed;
}

.flow,
.mind,
.koni {
  flex: 1;
}

#canvas_1{
  height:400px;
}
